body {
  width: 100%;
  height: 100vh;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center; 
}

@font-face {
  font-family: ringofire;
  src:local('ringofire'),
      url(/minesweeper/public/static/media/ringofire.2bdf579c.ttf)
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  height: 100%;
  width: 100%;
}
table, th, td {
    border: 1px solid black;
    border-collapse: collapse;
}

table {
    margin: 0 auto;
}

th, td {
    width: 24px;
    height: 24px;
}


@-webkit-keyframes explode {
    from { font-size: 3px; }
    to { font-size: 70px; }
}

@keyframes explode {
    from { font-size: 3px; }
    to { font-size: 70px; }
}

.modal {
    width: 100%;
    text-align: center;
}

.modal h1 {
    font-family: ringofire;
    font-size: 70px;
    -webkit-animation-name: explode;
            animation-name: explode;
    -webkit-animation-duration: 3s;
            animation-duration: 3s;
}


.about-me a {
    font-size: 30px;
    color: black;
}

.about-me p {
    font-family: Arial;
    font-size: 25px;
}
